:root {
  --theme-font-color: #373435;
  --primary-btn-color: #e5082b;
  --font-size-64 : 64px;
  --font-size-54 : 54px;
  --font-size-50 : 50px;
  --font-size-45 : 45px;
  --font-size-34 : clamp(34px, 3vw, 36px);
  --font-size-30 : clamp(30px, 3vw, 32px);
  --font-size-28 : clamp(28px, 3vw, 30px);
  --font-size-24 : clamp(24px, 3vw, 26px);
  --font-size-22 : clamp(22px, 3vw, 24px);
  --font-size-20 : clamp(20px, 3vw, 22px);
  --font-size-18 : clamp(18px, 3vw, 20px);
  --font-size-17 : clamp(17px, 3vw, 19px);
  --font-size-16 : clamp(16px, 3vw, 18px);
  --font-size-14 : clamp(14px, 3vw, 16px);
  --font-size-13 : clamp(13px, 3vw, 15px);
  --font-size-12 : clamp(12px, 3vw, 14px);
}
html {
    -webkit-text-size-adjust: 100%;
}
body {
    font-family: 'Outfit';
    font-size: var(--font-size-14);
    margin: 0;
    padding: 0;
    color: var(--theme-font-color);
}
body * {
    box-sizing: border-box;
}
a {
    font-size: inherit;
    color: inherit;
    text-decoration: none;
}
p, h1 ,h2 {
    margin: 0;
}
ul , li , ol {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

::placeholder {
  opacity: 0.5;
}

::-ms-input-placeholder {
  opacity: 0.5;
}

/* header */
.main-header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
}
.main-header .header-container {
    padding: 25px 40px 25px 30px;
    display: flex;
    align-items: center;
    gap:55px;
    background-color: #ffffff;
}
.header-logo {
    flex: 0 0 320px;
}
.header-logo img {
    max-width: 100%;
}
.header-nav {
    display: flex;
    align-items: center;
    gap:25px;
    flex: 1;
}
/* .header-nav .nav-link-dropdown {
    padding-right: 15px;
} */
.header-nav .nav-link-dropdown > a:after {
    content:url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='13' height='13' viewBox='0 0 512 512'><path d='M233.4 406.6c12.5 12.5 32.8 12.5 45.3 0l192-192c12.5-12.5 12.5-32.8 0-45.3s-32.8-12.5-45.3 0L256 338.7 86.6 169.4c-12.5-12.5-32.8-12.5-45.3 0s-12.5 32.8 0 45.3l192 192z'/></svg>");
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 0;
}
.header-nav .nav-link-dropdown > a {
    padding-right: 20px;
    position: relative;
}
.header-nav .nav-link-dropdown a {
    font-size: var(--font-size-18);
}
.header-btn .primary-btn {
    padding: 12px 30px;
}
.primary-btn {
    background-color: var(--primary-btn-color);
    display: inline-block;
    padding: 10px 25px;
    color: #ffffff;
    border-radius: 14px;
    font-weight: 700;
    letter-spacing: 0.5px;
    font-family: inherit;
    border-color: var(--primary-btn-color);
    box-shadow: none;
    outline: none;
    border: none;
    font-size: inherit;
}
.primary-link {
    background-color: transparent;
    display: inline-block;
    color: var(--primary-btn-color);
    font-weight: 700;
    letter-spacing: 0.5px;
    font-family: inherit;
    box-shadow: none;
    outline: none;
    border: none;
    font-size: inherit;
    border-bottom: 1px solid var(--primary-btn-color);
}
.secondary-btn {
    background-color: transparent;
    display: inline-block;
    padding: 10px 25px;
    /* color: #373435; */
    border: 1px solid #373435;
    border-radius: 14px;
    font-weight: 700;
    letter-spacing: 1;  
}
.header-marging {
    margin-top: 104px !important;
}


.sec-title {
    /* font-family: Outfit; */
    font-size: clamp(30px, 3vw, 45px);
    font-weight: 800;
    line-height: 1.5;
    text-align: center;
    /* color: #373435; */
    text-transform: uppercase;
    max-width: 870px;
    margin: 0 auto;
    margin-bottom: 50px;
}
.sec-title span , .sec-title h3 {
    display: block;
    /* font-family: Outfit; */
    font-size: var(--font-size-16);
    font-weight: 400;
    line-height: 22px;
    margin-top: 5px;
    text-transform: none;
}

