.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border-width: 0;
}
.dropdown-menu {
    position: absolute;
    transform: scaleY(0);
    transform-origin: 50% 0;
    transition: all 0.2s linear;
    padding: 10px;
}
.nav-link-dropdown:hover .dropdown-menu {
    transform: scaleY(1);
}
.dropdown-menu ul {
    background-color: #fff;
    border-radius: 18px;
    box-shadow: 0px 0px 50px 0px #072E7312;

}
.dropdown-menu ul li a {
    padding: 13px 30px;
    display: block;
}
.dropdown-menu ul li + li {
    border-top: 0.5px solid#e1e1e1;
}
.dropdown-menu ul li.active a {
    background-color: var(--primary-btn-color);
    color: #fff;
    border-radius: 18px;
}
.dropdown-menu ul li:hover a {
    background-color: var(--primary-btn-color);
    color: #fff;
    border-radius: 18px;
}
.dropdown-menu ul li.active {
    border-top-color: transparent;
    border-bottom-color: transparent;
}
.dropdown-menu ul li:hover {
    border-top-color: transparent;
    border-bottom-color: transparent;
}
.dropdown-menu ul li.active + li {
    border-top-color: transparent;
}
.dropdown-menu ul li:hover + li {
    border-top-color: transparent;
}
