.about-banner.banner-sec {
    padding: 100px 70px 0;
}
.about-banner.banner-sec .banner-container.about-banner {
    background-image: url('./about_banner.webp');
    position: relative;
    border-radius: 20px;
    background-position: center;
    background-size: cover;
}
.about-banner.banner-sec .banner-container img {
    width: 100%;
    height: auto;
    visibility: hidden;
}
.about-banner.banner-sec .banner-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1; /* Place it above the background */
    background: linear-gradient(180deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0.7) 100%);
    border-radius: 20px;
}

.about-banner .banner-content {
    z-index: 9;
    color: #fff;
    position: relative;
    margin: 0 auto;
    /* max-width: 635px; */
    width: 100%;
    padding: 70px 10px;
}
.about-banner .banner-content .sec-title {
    margin-bottom: 10px;
}
.about-banner .banner-desc {
    font-size: var(--font-size-20);
    line-height: 25.2px;
    text-align: center;
}
.about-banner .banner-btn {
    margin-top: 40px;
    text-align: center;
}
.abc{
    padding-bottom: 10px;
    padding-top: 10px;
}
@media screen and (max-width:928px) {
    .about-banner.banner-sec {
        padding: 100px 30px 0;
    }
}